/**
 * Base64 
 * @param str 
 * @returns Base64 
 */
export function base64Encode(str: string): string {
    return btoa(unescape(encodeURIComponent(str)))
}

/**
 * Base64 
 * @param str Base64 
 * @returns 
 */
export function base64Decode(str: string): string {
    return decodeURIComponent(escape(atob(str)))
}